/* Global styles */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #121212;
  color: #ddd;
  line-height: 1.5;
  transition: background-color 0.3s ease;
}

.App {
  text-align: center;
  padding: 20px;
}

.App-header {
  background-color: #1a1a1a;
  padding: 25px 20px;
  border-radius: 12px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
  position: relative;
  z-index: 1;
}

.App-header h1 {
  font-size: 2.5rem;
  color: #fff;
  margin-bottom: 10px;
}

.App-header h2 {
  font-size: 1.5rem;
  color: #ccc;
}

.App-header p {
  font-size: 1rem;
  color: #bbb;
}

.portfolio-links {
  margin-top: 15px;
}

.github-link {
  font-size: 1rem;
  color: #61dafb;
  text-decoration: none;
  font-weight: bold;
  padding: 8px 15px;
  border-radius: 6px;
  background-color: #333;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.github-link:hover {
  background-color: #444;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
  transform: scale(1.05);
}

/* Tabs */
.tabs {
  display: flex;
  justify-content: center;
  margin-top: 30px;
  gap: 10px;
}

.tab {
  font-size: 1rem;
  padding: 10px 20px;
  background-color: #333;
  color: #bbb;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.tab:hover {
  background-color: #444;
  transform: scale(1.05);
}

.tab.active {
  background-color: #61dafb;
  color: #121212;
}

section {
  margin-top: 30px;
  padding: 20px;
  background-color: #1e1e1e;
  border-radius: 12px;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease;
}

h2 {
  font-size: 1.4rem;
  color: #fff;
  margin-bottom: 10px;
}

/* Hover effect for projects */
.projects .project {
  margin-top: 15px;
  background-color: #2c2c2c;
  border-radius: 8px;
  padding: 15px;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.projects .project:hover {
  transform: translateY(-5px);
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.3);
}

.projects .project h3 {
  font-size: 1.3rem;
  color: #61dafb;
  margin-bottom: 8px;
}

/* GitHub Stats */
.github-stats-img {
  display: flex;
  justify-content: center;
  gap: 15px;
}

.github-stats-img img {
  border-radius: 8px;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.2);
  width: 45%;
}

/* Scroll to Top Button */
.scroll-to-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background-color: #61dafb;
  color: #121212;
  border: none;
  padding: 15px;
  border-radius: 50%;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  font-size: 1.5rem;
  display: none;
}

.scroll-to-top:hover {
  background-color: #444;
  transform: scale(1.1);
}

.scroll-to-top.show {
  display: block;
}

/* Dark Mode & Light Mode */
.dark-mode {
  background-color: #121212;
  color: #ddd;
}

.light-mode {
  background-color: #f0f0f0;
  color: #121212;
}

/* Dark Mode Toggle */
.dark-mode-toggle {
  margin-top: 20px;
}

.dark-mode-toggle label {
  font-size: 1.2rem;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  cursor: pointer;
}

/* Custom Dark Mode Toggle */
.dark-mode-toggle input {
  appearance: none;
  width: 50px;
  height: 25px;
  border-radius: 50px;
  background-color: #ccc;
  position: relative;
  transition: background-color 0.3s ease;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
}

.dark-mode-toggle input:checked {
  background-color: #61dafb;
}

.dark-mode-toggle input::before {
  content: "";
  position: absolute;
  top: 3px;
  left: 3px;
  width: 19px;
  height: 19px;
  border-radius: 50%;
  background-color: #fff;
  transition: transform 0.3s ease;
}

.dark-mode-toggle input:checked::before {
  transform: translateX(25px);
}

.dark-mode-toggle input:focus {
  outline: none;
  box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.3);
}
